body {
  font-family: 'Open Sans', sans-serif;
  padding: 10px;
  display: flex;
  flex-direction: column;
}

a, a:visited {
  color: #03a9f4;
}

header {
  display: flex;
  justify-content: flex-end;
  width: 800px;
  margin: 0 auto 15px;
}

header a {
  margin-right: 10px;
  text-decoration: none;
}

main {
  width: 800px;
  margin: 0 auto;
}

img.screenshot {
  width: 90%;
  margin: 0 auto;
}

.features {
  margin-top: 100px;
}

.feature img {
  width: 100%;
}

.subscription {
  margin: 70px 0 70px;
}

